.preloader {
  position: fixed;
  z-index: 100000;
  width: 100%;
  height: 100%;
  background: var(--color-background);
  &__icon {
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    width: 12vw;
    height: 12vw;
    margin: auto;
    opacity: 0;
    visibility: hidden;
  }
  &__back {
    color: var(--color-preloader);
  }
}